.logo-wrapper {
    position: relative;
    display: inline-block;
    white-space: nowrap;
    font-size: 32px;
    color: white;
    text-align: center;
    width: 100%;
    font-family: 'Pacifico', cursive;
}
  
.logo {
    width: 29px;
    position: relative;
    top: 2px;
    left: 1px;
    transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
}

.logo-animation {
  animation: logo-anim 1250ms cubic-bezier(0.73,-0.74, 0.41, 1.47) infinite;
}

@keyframes logo-anim {
  0% {transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg)}
  100% {transform: rotateX(0deg) rotateY(0deg) rotateZ(360deg)}
}

@media screen and (max-width: 1024px) {
  .logo-wrapper {
    font-size: 28px;
    top: 5px;
  }

  .logo {
    width: 25px;
  }
}

@media screen and (max-width: 768px) {
  .logo-wrapper {
    font-size: 24px;
    top: 8px;
  }

  .logo {
    width: 22px;
  }
}

@media screen and (max-width: 375px) {
  .logo-wrapper {
    font-size: 20px;
    top: 10px;
  }

  .logo {
    width: 18px;
  }
}

@media screen and (min-width: 376px) and (max-height: 800px) {
  .logo-wrapper {
    font-size: 24px;
    top: 8px;
  }

  .logo {
    width: 22px;
  }
}

@media screen and (min-width: 376px) and (max-height: 550px) {
  .logo-wrapper {
    font-size: 20px;
    top: 10px;
  }

  .logo {
    width: 18px;
  }
}