.shopping-cart-icon-wrapper {
    position: absolute;
    text-align: center;
    left: 100%;
    transform: translate(calc(-100% - 10px), -50%);
    width: 80px;
    white-space: nowrap;
    top: 50%;
    height: calc(50% - 5px);
    color: #b4efcb;
    border-radius: 7px;
    background: #5d8e50;
    padding: 6px 1px;
    cursor: pointer;
    transition-property: transform;
    transition-duration: 400ms;
    transition-timing-function:cubic-bezier(0.23, 1, 0.320, 1);
  }

  .added-item {
    animation: scale 1s ease-in-out infinite;
  }

  @keyframes scale {
    from { transform: translate(calc(-100% - 10px), -50%) scale(1); }
    25% { transform: translate(calc(-100% - 10px), -50%) scale(1.05); }
    50% { transform: translate(calc(-100% - 10px), -50%) scale(1); }
    75% { transform: translate(calc(-100% - 10px), -50%) scale(1.05); }
    to { transform: translate(calc(-100% - 10px), -50%) scale(1); }
  }
  
  .shopping-cart-icon-wrapper:hover {
    background: #4e7942;
  }
  
  .shopping-cart-icon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotateX(0deg) rotateY(0deg) rotateZ(0deg);
    transform-origin: center center;
    width: 24px;
    transition: transform 500ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }

  .shopping-cart-icon-wrapper:hover {
    transform: translate(calc(-100% - 10px), -50%) scale(1.1);
  }

  .shopping-cart-icon-wrapper:hover .shopping-cart-icon {
    transform: translate(-50%, -50%) rotateX(0deg) rotateY(0deg) rotateZ(360deg);
  }

  @media screen and (max-width: 768px) {
    .shopping-cart-icon-wrapper {
      width: 60px;
      height: calc(50% - 10px);
    }

    .shopping-cart-icon {
      width: 20px;
    }
  }

  @media screen and (max-width: 425px) {
    .shopping-cart-icon-wrapper {
      width: 40px;
      height: calc(50% - 12px);
    }

    .shopping-cart-icon {
      width: 16px;
    }
  }

  @media screen and (max-width: 320px) {
    .shopping-cart-icon-wrapper {
      width: 30px;
      height: calc(50% - 15px);
    }

    .shopping-cart-icon {
      width: 14px;
    }
  }

  @media screen and (max-height: 800px) {
    .shopping-cart-icon-wrapper {
      width: 60px;
      height: calc(50% - 10px);
    }

    .shopping-cart-icon {
      width: 20px;
    }
  }

  @media screen and (max-height: 550px) {
    .shopping-cart-icon-wrapper {
      width: 50px;
      height: calc(50% - 15px);
    }

    .shopping-cart-icon {
      width: 18px;
    }
  }