  .menu-list-item-container {
    position: relative;
    display: inline-block;
    width: auto;
    height: auto;
    margin: 10px 35px;
    vertical-align: top;
    max-width: 250px;  
}
  
  .menu-list-image {
    position: relative;
    width: 250px;
    height: 250px;
    transition-property: transform;
    transition-duration: 500ms;
    transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1);
}

.menu-list-image:hover {
  transform: scale(1.1);
}
  
  .menu-list-item-name {
    text-align: center;
    font-size: 18px;
    margin-top: 5px;
    font-weight: 800;
}
  
  .counter-wrapper {
    position: relative;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    user-select: none;
    z-index: 99;
}

.arrows-container {
  width: 95px;
  display: flex;
  white-space: nowrap;
  height: 30px;
  align-items: center;
  z-index: 9;
  justify-content: center;
}
  
  .counter-item {
    display: inline-block;
    margin: 0;
    font-size: 20px;
    font-weight: bold;
    color: #efa436;
    vertical-align: middle;
    outline: none;
    border: none;
}
  
  .left-arrow, .right-arrow {
    cursor: pointer;
    font-size: 36px;
    transition: color .2s ease-in, opacity 150ms ease;
    vertical-align: middle;
    margin: 0;
    padding: 0 10px;
    border: none;
    background-color: transparent;
    font-weight: 600;
    font-family: 'Prompt', sans-serif;
}

.left-arrow:hover, .right-arrow:hover {
  color: #b47312;
}
  
  .add-to-shopping-cart-btn {
    position: relative;
    display: inline-block;
    padding: 1px 18px;
    width: auto;
    height: auto;
    text-align: center;
    background: transparent;
    cursor: pointer;
    color: #efa436;
    border: 2px solid #efa436;
    border-radius: 5px;
    transition: color .2s ease-in, border-color .2s ease-in, opacity 150ms ease;
    font-size: 14px;
    z-index: 99;
    font-family: 'Architects Daughter', cursive;
    vertical-align: middle;
    margin-left: 10px;
}

.add-to-shopping-cart-btn:hover {
  border-color: #b47312;
  color: #b47312;
}

.menu-list-item-price {
  display: inline-block;
  margin: 3px auto 0;
  font-weight: 800;
}
  
  .menu-list-item-description {
    margin: 2px;
}
  
  .menu-list-item-size-container {
    position: relative;
    font-size: 14px;
    font-family: 'Indie Flower', cursive;
    margin: 10px;
    z-index: 999;
}
  
  .pizza-size {
    display: inline-block;
    margin: 0 6px;
    font-weight: 800;
    border-radius: 15px;
    padding: 6px 10px;
    user-select: none;
    cursor: pointer;
    border: none;
    transition: background-color, color;
    transition-duration: 600ms;
    transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1);
}

  .pizza-size, .pizza-size:disabled {
    background-color: #f3deab;
    font-family: 'Architects Daughter', cursive;
    color: #c2872f;
    font-size: 14px;
}

.pizza-size:hover {
  background-color: #d17d00;
  color: #f3deab;
}
  
  .menu-list-item-price {
    font-family: 'Architects Daughter', cursive;
    font-size: 24px;
    color: #efa436;
}

.selected, .selected:disabled, .selected:hover {
    background: #b12720;
    font-family: 'Architects Daughter', cursive;
    color: #e0d5b9;
}

.hide {
    animation: hide .25s ease-out;
}
  
.show {
    animation: show .25s ease-out;
}

@keyframes hide {
    from {background: #a5b5c3;}
    to {background: #dadada;}
}

@keyframes show {
    from {background: #dadada;}
    to {background: #a5b5c3;}
}

