.middle-wrapper-title {
    position: relative;
    width: 100%;
    font-size: 28px;
    text-align: center;
    margin: 0;
    top: 25px;
    color: #0c0c0c;
    font-weight: 600;
}
  
  .why-us-list {
    position: relative;
    margin: 30px auto 0;
    width: calc(100% - 20px);
    padding: 0 10px;
    max-width: 1660px;
    height: auto;
    text-align: center;
    list-style-type: none;
}
  
  .why-us-list-item-container {
    position: relative;
    display: inline-block;
    width: auto;
    height: auto;
    margin: 20px 25px 0;
    vertical-align: top;
    width: 425px;
}
  
  .why-us-image {
    width: 100%;
    height: 255px;
    border-radius: 5px;
    transform: scale(1);
    transition-property: transform;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.25, 0.4, 0.2, 1);
}

.why-us-image:hover {
  transform: scale(1.1);
}
  
  .why-us-list-item {
    position: relative;
    margin: 7px 0;
    font-size: 16px;
}

@media screen and (max-width: 1024px) {
  .why-us-list-item-container {
    width: 340px;
  }

  .why-us-image {
    height: 204px;
  }
}

