.shopping-cart-wrapper {
  position: fixed;
  border-radius: 25px;
  box-shadow: 20px 20px 100px #000000;
  padding: 5px;
  margin: 0;
  width: 650px;
  height: auto;
  background-color: white;
  top: 50%;
  left: 50%;
  font-family: 'Architects Daughter', cursive;
  font-size: 20px;
  transform: translate(-50%, -50%);
  font-weight: bold;
  color: black;
  min-width: 200px;
  z-index: 9999;
}

.empty-shopping-cart-text {
  position: relative;
  margin: 16px 0;
  padding: 0;
  width: 100%;
  height: auto;
  text-align: center;
}

.empty-cart-icon {
  visibility: hidden;
  width: 60px;
  height: 60px;
  transition-property: transform;
  transition-duration: 1250ms;
  transition-timing-function: cubic-bezier(0.68, -0.66, 0.265, 1.33);
  transform: rotateX(0deg) rotateY(0deg) rotateZ(-360deg);
}

.empty-cart-icon:hover {
  visibility: visible;
  transform: rotateX(0deg) rotateY(0deg) rotateZ(360deg);
}

.empty-cart-icon-animation {
  visibility: visible;
  animation: empty-cart 2000ms cubic-bezier(0.175, 0.885, 0.32, 1.275) 1;
}

@keyframes empty-cart {
  0% {transform: translateX(-250%) rotateX(0deg) rotateY(0deg) rotateZ(0deg)}
  8% {transform: translateX(100%), rotateX(0deg) rotateY(0deg) rotateZ(0deg)}
  25% {transform: translateX(0) rotateX(0deg) rotateY(0deg) rotateZ(0deg)}
  33% {transform: translateX(0) rotateX(0deg) rotateY(0deg) rotateZ(0deg)}
  66% {transform: translateX(0) rotateX(0deg) rotateY(0deg) rotateZ(360deg)}
  100% {transform: translateX(0) rotateX(0deg) rotateY(0deg) rotateZ(0deg)}
}

.shopping-cart-list {
  position: relative;
  margin: 2px auto 0;
  top: 0;
  left: 0;
  width: calc(100% - 16px);
  height: auto;
  max-height: 500px;
  list-style-type: none;
  overflow-x: hidden;
  overflow-y: auto;
  padding: 0 3px;
}

.shopping-cart-btns-wrapper {
  position: relative;
  left: 0;
  width: 100%;
  height: auto;
  margin: 5px 0;
  text-align: center;
}

.shopping-cart-btn {
  font-size: 16px;
  cursor: pointer;
  margin: 5px;
  color: #e0d5b9;
  background: #c2872f;
  border: 1px solid #725223;
  border-radius: 15px;
  padding: 2px 10px;
  font-family: 'Architects Daughter', cursive;
  transition: background-color .3s ease-out, color .3s ease-out, border-color .3s ease-out;
}

.shopping-cart-btn:hover {
  color: #c2872f;
  background-color: #e0d5b9;
  border: 1px solid #c2872f;
}

.shopping-cart-total-price {
  font-size: 16px;
}

.total-price-content {
  font-size: 18px;
}

.shopping-cart-btn-icon {
  width: 16px;
  height: 16px;
  top: 3px;
  position: relative;
  margin-right: 5px;
}

.total-items-price {
  margin: 10px auto 5px;
  padding: 0;
  width: 100%;
  text-align: center;
  font-size: 16px;
  color: #c2872f;
}

