.menu-container {
    position: relative;
    top: 0;
    left: 0;
    width: calc(100% - 40px);
    padding: 10px 20px;
    height: auto;
}
  
  .menu-toggle-list {
    margin: 0 auto;
    padding: 0;
}
  
  .menu-toggle-list-item {
    position: relative;
    display: inline-block;
    margin: 5px 30px;
    padding: 4px 0;
    font-size: 20px;
    font-weight: 800;
    cursor: pointer;
    border-bottom: 1px solid transparent;
    color: #000;
}
  
  .menu-list-item-selected {
    border-bottom: 1px solid #ccacac;
}

.menu-list-item-selected:hover {
  color: #000;
}
  
  .menu-list {
    position: relative;
    margin: 25px auto 15px;
    width: 100%;
    max-width: 1550px;
    height: auto;
    text-align: center;
    list-style-type: none;  
}

@media screen and (max-width: 1024px) {
  .menu-toggle-list-item {
    font-size: 18px;
    margin: 4px 25px;
    padding: 3px 0;
  }
}

@media screen and (max-width: 610px) {
  .menu-toggle-list-item {
    font-size: 16px;
    margin: 4px 20px;
    padding: 3px 0;
  }
}

@media screen and (max-height: 550px) {
  .menu-toggle-list-item {
    font-size: 16px;
    margin: 4px 20px;
    padding: 3px 0;
  }
}

@media screen and (max-height: 400px) {
  .menu-toggle-list-item {
    font-size: 14px;
    margin: 3px 15px;
    padding: 3px 0;
  }
}