.pizza-of-the-month-main-container {
    position: relative;
    margin: 50px auto 15px;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    background: #793d3d;
    color: white;
  }
  
  .pizza-of-the-month-wrapper {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: auto;
    height: auto;
    text-align: center;
    margin: 0;
    top: 0;
    padding: 30px;
    background: #793d3d;
  }
  
  .heart-icon {
    width: 450px;
    margin-top: 30px;
    animation: heartbeat .5s cubic-bezier(0,-0.32, 0.66, 2) infinite;
  }
  
  @keyframes heartbeat {
    0% { transform: scale(1); }
    50% { transform: scale(1.1); }
    100% { transform: scale(1); }
  }
  
  .pizza-bestseller-img, .pizza-of-the-month-description {
    display: inline-block;
    vertical-align: top;
  } 
  
  .pizza-bestseller-img {
    position: relative;
    width: 660px;
    height: 510px;
    border: 10px solid white;
    border-radius: 270px;
    transform-origin: center center;
    transform: scale(1);
    transition: transform 250ms cubic-bezier(0.44, 0.2, 0.56, 0.86);
  }

  .pizza-bestseller-img:hover {
    transform: scale(1.1);
  }
  
  .pizza-of-the-month-description {
    position: relative;
    margin-left: 25px;
    max-width: 680px;
    padding: 0 5px;
  }
  
  .pizza-of-the-month-title {
    text-align: center;
    font-size: 24px;
  }
  
  .pizza-of-the-month-content, .pizza-of-the-month-price-description, .pizza-of-the-month-price {
    text-align: center;
    font-size: 18px;
  }
  
  .pizza-of-the-month-price {
    color: #f8a7a0cc;
    font-weight: 600;
  }

