    .pizza-of-the-month-main-container {
      margin: 25px auto 10px;
    }

    .pizza-of-the-month-wrapper {
      padding: 25px;
    }

    .pizza-bestseller-img {
      width: 528px;
      height: 408px;
      border: 8px solid white;
      border-radius: 220px;
    }

    .heart-icon {
      width: 360px;
    }

    .pizza-of-the-month-description {
