.where-to-find-us-wrapper {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    background-color: white;
    padding: 30px 0;
  }
  
  .where-to-find-us-title {
    position: relative;
    margin: 0;
    width: 100%;
    text-align: center;
    font-family: 'Architects Daughter', cursive;
  }
  
  .where-to-find-us-list {
    position: relative;
    margin: 0;
    padding: 0;
    width: 100%;
    list-style-type: none;
    font-family: 'Prompt', sans-serif;
  }
  
  .where-to-find-us-list-item {
    position: relative;
    margin: 10px auto 0;
    font-size: 16px;
  }
  
  .where-to-find-us-list-item-wrapper {
    position: relative;
    display: block;
    width: auto;
    height: auto;
    text-align: center;
    margin: 30px auto;
  }

  .where-to-find-us-last-list-item-wrapper {
    margin: 30px auto 0;
  }
  
  .where-to-find-us-icon {
    width: 50px;
    height: 50px;
    transition-property: transform;
    transition-duration: 500ms;
    transition-timing-function: cubic-bezier(0.25, 0.4, 0.2, 1);
    transform: scale(1);
  }

  .where-to-find-us-icon:hover {
    transform: scale(1.25);
  }

  @media screen and (max-width: 768px) {
    .where-to-find-us-title {
      font-size: 20px;
    }

    .where-to-find-us-list-item {
      font-size: 14px;
      margin: 6px auto 0;
    }

    .where-to-find-us-icon {
      width: 40px;
      height: 40px;
    }

    .where-to-find-us-list-item-wrapper {
      margin: 20px auto;
    }

    .where-to-find-us-last-list-item-wrapper {
      margin: 20px auto 0;
    }

    .where-to-find-us-wrapper {
      padding: 20px 0;
    }
  }

  @media screen and (max-width: 425px) {
    .where-to-find-us-title {
      font-size: 18px;
    }

    .where-to-find-us-list-item {
      font-size: 12px;
      margin: 4px auto 0;
    }

    .where-to-find-us-icon {
      width: 30px;
      height: 30px;
    }
  }

  @media screen and (min-width: 426px) and (max-height: 800px) {
    .where-to-find-us-title {
      font-size: 16px;
    }

    .where-to-find-us-list-item {
      font-size: 11px;
      margin: 4px auto 0;
    }

    .where-to-find-us-icon {
      width: 30px;
      height: 30px;
    }

    .where-to-find-us-list-item-wrapper {
      margin: 15px auto;
    }

    .where-to-find-us-last-list-item-wrapper {
      margin: 15px auto 0;
    }

    .where-to-find-us-wrapper {
      padding: 15px 0;
    }
  }

  @media screen and (min-width: 426px) and (max-height: 550px) {
    .where-to-find-us-title {
      font-size: 14px;
    }

    .where-to-find-us-list-item {
      font-size: 10px;
      margin: 3px auto 0;
    }

    .where-to-find-us-icon {
      width: 24px;
      height: 24px;
    }

    .where-to-find-us-list-item-wrapper {
      margin: 10px auto;
    }

    .where-to-find-us-last-list-item-wrapper {
      margin: 10px auto 0;
    }

    .where-to-find-us-wrapper {
      padding: 10px 0;
    }
  }

  @media screen and (min-width: 769px) and (max-height: 800px) {
    .where-to-find-us-title {
      font-size: 20px;
    }

    .where-to-find-us-list-item {
      font-size: 14px;
      margin: 6px auto 0;
    }

    .where-to-find-us-icon {
      width: 40px;
      height: 40px;
    }

    .where-to-find-us-list-item-wrapper {
      margin: 20px auto;
    }

    .where-to-find-us-last-list-item-wrapper {
      margin: 20px auto 0;
    }

    .where-to-find-us-wrapper {
      padding: 20px 0;
    }
  }

  @media screen and (min-width: 769px) and (max-height: 550px) {
    .where-to-find-us-title {
      font-size: 16px;
    }

    .where-to-find-us-list-item {
      font-size: 11px;
      margin: 4px auto 0;
    }

    .where-to-find-us-icon {
      width: 30px;
      height: 30px;
    }

    .where-to-find-us-list-item-wrapper {
      margin: 15px auto;
    }

    .where-to-find-us-last-list-item-wrapper {
      margin: 15px auto 0;
    }

    .where-to-find-us-wrapper {
      padding: 15px 0;
    }
  }