.shopping-cart-list-item-wrapper {
    position: relative;
    margin: 10px 0;
    display: block;
    width: calc(100% - 6px);
    height: 100px;
    transition-property: transform;
    transition-duration: 500ms;
    transition-timing-function: cubic-bezier(0.68, -1.66, 0.265, 1.33);
    overflow: hidden;
  }

  .shopping-cart-border {
    position: absolute;
    margin: 0;
    border: 2px dashed #c2872f;
    border-radius: 50px;
    display: block;
    width: calc(100% - 6px);
    height: calc(100% - 6px);
    z-index: 9;
    overflow: hidden;
  }

  .shopping-cart-delete-btn {
    position: absolute;
    margin: 0;
    top: 50%;
    left: 100%;
    transform: translate(-150%, -50%);
    border-radius: 0px;
    border: none;
    background-color: transparent;
    padding: 0px 5px;
    cursor: pointer;
    transition-property: color;
    transition-duration: 750ms;
    transition-timing-function: cubic-bezier(0.165, 0.84, 0.44, 1);
    z-index: 99;
    font-size: 32px;
    color: #c2872f;
    outline: none;
  }

  .shopping-cart-delete-btn:hover {
    color: #b12720;
  }

  .delete-item-icon {
    width: 20px;
    position: relative;
    top: 1px;
  }

  .added-item-img {
    position: absolute;
    top: 4.5px;
    left: 6px;
    width: 90px;
    height: 90px;
    transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg) scale(1);
    transition-property: transform;
    transition-duration: 500ms;
    transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
    z-index: 999;
  }

  .added-item-img:hover {
    transform: rotateX(0deg) rotateY(0deg) rotateZ(-360deg) scale(0.75);
  }

  .shopping-cart-list-item {
    position: relative;
    display: inline-block;
    width: auto;
    top: 1.5px;
    height: auto;
    text-align: left;
    z-index: 99;
  }

  .shopping-cart-item-count {
    margin-left: 15px;
  }

  .shopping-cart-total-price {
    display: block;
    width: 100%;
    text-align: center;
    top: calc(100% - 60px);
    font-size: 16px;
  }

  .total-price-content {
    font-size: 18px;
    color: #c2872f;
  }

  @media only screen and (hover: none) and (pointer: coarse){
    .shopping-cart-delete-btn:hover {
      color: #c2872f;
      user-select: none;
    }
  }