.main-title-container {
    position: relative;
    width: 950px;
    height: auto;
    margin: 0 auto;
    top: calc(50% - 180px);
}
  
  .main-title {
    font-size: 54px;
    margin: 0;
    padding: 0;
    text-align: center;
    color: white;
}
  
  .main-description {
    font-size: 24px;
    margin: 0;
    padding: 0;
    text-align: center;
    color: white;
}

@media screen and (max-width: 1440px) {
  .main-title-container {
    width: 770px;
    top: calc(50% - 170px);
  }

  .main-title {
    font-size: 48px;
  }

  .main-description {
    font-size: 22px;
  }
}

@media screen and (max-width: 1280px) {
  .main-title-container {
    width: 715px;
    top: calc(50% - 165px);
  }
  
  .main-title {
    font-size: 44px;
  }

  .main-description {
    font-size: 20px;
  }
}

