.first-menu-look-list {
  position: absolute;
  margin: 0;
  top: calc(100% - 220px);
  padding: 15px 0;
  left: 0;
  width: 100%;
  height: auto;
  color: white;
  font-weight: 600;
  list-style-type: none;
  text-align: center;
  border-top: 1px solid #f5dfdf;
  border-bottom: 1px solid #f5dfdf;
  overflow: hidden;
}

.first-menu-look-list-item-wrapper {
  position: relative;
  display: inline-block;
  width: auto;
  height: auto;
  margin: 10px 40px;
  transition-property: transform;
  transition-duration: 300ms;
  transition-timing-function: cubic-bezier(0.25, 0.1, 0, 3);
}

.first-menu-look-list-item-wrapper:hover {
  transform: scale(1.2);
}

.first-menu-look-list-item {
  position: relative;
  width: auto;
  height: auto;
  display: inline-block;
  font-size: 28px;
}

.first-menu-look-list-item-icon {
  display: inline-block;
  width: 28px;
  height: 28px;
  vertical-align: sub;
  margin-left: 5px;
}

@media screen and (max-width: 1440px) {
  .first-menu-look-list {
    top: calc(100% - 210px);
  }

  .first-menu-look-list-item {
    font-size: 24px;
  }

  .first-menu-look-list-item-icon {
    width: 24px;
    height: 24px;
  }

  .first-menu-look-list-item-wrapper {
    margin: 8px 32px;
  }
}

@media screen and (max-width: 1024px) {
  .first-menu-look-list {
    top: calc(100% - 190px);
  }

  .first-menu-look-list-item {
    font-size: 20px;
  }

  .first-menu-look-list-item-icon {
    width: 20px;
    height: 20px;
  }

  .first-menu-look-list-item-wrapper {
    margin: 5px 20px;
  }
}

@media screen and (max-width: 768px) {
  .first-menu-look-list-item {
    font-size: 18px;
  }

  .first-menu-look-list-item-icon {
    width: 18px;
    height: 18px;
  }
}

@media screen and (max-width: 425px) {
  .first-menu-look-list {
    top: calc(100% - 205px);
    padding: 10px 0;
  }

  .first-menu-look-list-item {
    font-size: 16px;
  }

  .first-menu-look-list-item-icon {
    width: 16px;
    height: 16px;
  }
}

@media screen and (max-width: 320px) {
  .first-menu-look-list-item {
    font-size: 14px;
  }

  .first-menu-look-list-item-icon {
    width: 14px;
    height: 14px;
  }

  .first-menu-look-list-item-wrapper {
    margin: 3px 12px;
  }
}

