    width: 14px;
    height: 14px;
  }
}

@media screen and (min-width: 769px) and (max-height: 500px) {
  .first-menu-look-list {
    top: calc(100% - 130px);
    padding: 10px 0;
  }

  .first-menu-look-list-item {
