nav {
  position: absolute;
  top: 0;
  left: 0;
  padding: 0;
  width: fit-content;
  max-width: 60px;
  height: 100%;
  z-index: 999;
  vertical-align: top;
}

.menu-toggle-container {
  position: relative;
  top: 50%;
  left: 15px;
  width: fit-content;
  height: fit-content;
  transform-origin: center center;
  transform: translateY(-50%);
}

.navbar-item {
  position: relative;
  display: inline-block;
  list-style: none;
  font-size: 18px;
  top: calc(50% - 12px);
  margin: 0 10px;
  font-weight: bold;
  font-family: 'Prompt', sans-serif;
}

.navbar-item-link {
  text-decoration: none;
  color: aliceblue;
  cursor: pointer;
  transition: color .25s ease;
}

.navbar-item-link:hover {
  color: #c9dbeb;
}

.navigation-main-container {
  position: relative;
  display: block;
  top: 31px;
  width: 300px;
  transition: transform 500ms cubic-bezier(0.43,-0.21, 0, 1.48);
  transform: translateX(-100%);
  z-index: 999;
}

.navigation-container {
  position: relative;
  display: block;
  top: 0;
  background-color: #171021;
  width: calc(100% - 12px);
  height: auto;
  padding: 6px;
  border-top: 1px solid #cdcdcd;
  text-align: center;
}

.navbar-item {
  display: block;
  top: 0;
  font-size: 16px;
  margin: 10px 0;
}

#menu-toggle {
  display: block;
  position: relative;
  width: fit-content;
  height: fit-content;
  top: 0;
  left: 0;
  z-index: 1;
  -webkit-user-select: none;
  user-select: none;
}

#menu-toggle input {
  display: block;
  width: 40px;
  height: 32px;
  position: absolute;
  top: -7px;
  left: -5px;
  cursor: pointer;
  opacity: 0; 
  z-index: 2; 
  -webkit-touch-callout: none;
}

#menu-toggle span {
  display: block;
  width: 33px;
  height: 4px;
  margin-bottom: 5px;
  position: relative;
  background: #cdcdcd;
  border-radius: 3px;
  z-index: 1;
  transform-origin: 4px 0px;
  transition: transform 0.5s cubic-bezier(0.77 ,0.2, 0.05, 1.0), background-color 0.5s cubic-bezier(0.71, 0.18, 0.41, 0.95), opacity 0.25s cubic-bezier(0.71, 0.18, 0.41, 0.95);
}

#menu-toggle span:first-child {
  transform-origin: 0% 0%;
}

#menu-toggle span:nth-last-child(2) {
  transform-origin: 0% 100%;
}

#menu-toggle input:checked ~ span {
  opacity: 1;
  transform: rotateX(0deg) rotateY(0deg) rotateZ(45deg) translate(-2px, -1px);
  background: #313131;
}

#menu-toggle input:checked ~ span:nth-last-child(3) {
  opacity: 0;
  transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg) scale(0.2, 0.2);
}

#menu-toggle input:checked ~ span:nth-last-child(2) {
  transform: rotateX(0deg) rotateY(0deg) rotateZ(-45deg) translate(0, -1px);
}

@media screen and (max-width: 768px) {
  .navbar-item {
    font-size: 14px;
    margin: 8px 0;
  }

  .navigation-main-container {
    width: 200px;
  }

  .menu-toggle-container {
    transform: translateY(-50%) scale(.9);
  }
}

@media screen and (max-width: 445px) {
  .menu-toggle-container {
    transform: translateY(-50%) scale(.75);
  }

  .navigation-main-container {
    width: 150px;
  }

  .navbar-item {
    font-size: 12px;
  }
}

@media screen and (max-width: 320px) {
  .navbar-item {
    font-size: 11px;
    margin: 5px 0;
  }

  .navigation-main-container {
    width: 100px;
    margin: 3px 0;
  }

  .menu-toggle-container {
    transform: translateY(-50%) scale(.6);
  }
}

@media screen and (max-width: 240px) {
  .navigation-main-container {
    width: 75px;
    margin: 3px 0;
  }
}

@media screen and (min-width: 769px) and (max-height: 800px) {
  .navbar-item {
    font-size: 14px;
    margin: 8px 0;
  }

  .menu-toggle-container {
    transform: translateY(-50%) scale(.9);
  }
}

@media screen and (min-width: 769px) and (max-height: 550px) {
  .navbar-item {
    font-size: 12px;
    margin: 6px 0;
  }

  .menu-toggle-container {
    transform: translateY(-50%) scale(.75);
  }
}

@media screen and (min-width: 769px) and (max-height: 400px) {
  .navbar-item {
    font-size: 10px;
    margin: 4px 0;
  }
}
