.close-header-btn-container {
    position: absolute;
    top: 0;
    left: 100%;
    transform: translateX(20%);
    transform-origin: center center;
    padding: 0;
    margin: 0;
    width: auto;
    height: auto;
}

#close-header-btn {
    position: relative;
    top: 0;
    left: 0;
    background-color: transparent;
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
    font-size: 42px;
    color: #e6d16b;
    cursor: pointer;
    font-weight: 600;
    transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
    transform-origin: center center;
    transition: transform 250ms cubic-bezier(0.26,-0.03, 0.18, 1.99), color 250ms ease;
    font-family: 'Architects Daughter', cursive;
}

#close-header-btn:hover {
    color: #5d8e50;
    transform: rotateX(0deg) rotateY(0deg) rotateZ(-180deg);
}

@media screen and (max-width: 768px) {
    #close-header-btn {
        font-size: 36px;
    }
  }
  
  @media screen and (max-width: 445px) {
    #close-header-btn {
        font-size: 28px;
    }
  }
  
  @media screen and (max-width: 320px) {
    #close-header-btn {
        font-size: 24px;
    }
  }
  
  @media screen and (max-width: 240px) {
    #close-header-btn {
        font-size: 20px;
    }
  }
  
  @media screen and (min-width: 769px) and (max-height: 800px) {
    #close-header-btn {
        font-size: 36px;
    }
  }
  
  @media screen and (min-width: 769px) and (max-height: 550px) {
    #close-header-btn {
        font-size: 28px;
    }
  }
  
  @media screen and (min-width: 769px) and (max-height: 400px) {
    #close-header-btn {
        font-size: 20px;
    }
  }