.contact-us-wrapper {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
  }

  .contact-us-background {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: brightness(0.6);
  }
  
  .contact-us-form {
    position: relative;
    width: 60%;
    padding: 10px 0;
    height: auto;
    margin: 0 30px auto auto;
    top: 0;
    background: transparent;
    color: white;
  }
  
  .contact-us-main-title {
    font-size: 32px;
    margin: 0;
    font-family: 'Architects Daughter', cursive;
    text-align: center;
  }
  
  .contact-us-title {
    margin: 15px auto;
    font-family: 'Architects Daughter', cursive;
    text-align: center;
    font-size: 20px;
  }
  
  .contact-us-input {
    margin: 25px auto 0;
    position: relative;
    display: block;
    width: 75%;
    border: none;
    border-bottom: 1px solid #e4dcdc;
    background: transparent;
    padding: 5px;
    text-align: center;
    color: white;
    font-family: 'Prompt', sans-serif;
    font-size: 14px;
    border-radius: 1px;
  }
  
  .contact-us-textarea {
    resize: none;
    height: 100px;
  }
  
  .contact-us-textarea::-webkit-scrollbar {
    width: 10px;
    background-color: #f9f9fd;
  }
  
  .contact-us-textarea::-webkit-scrollbar-thumb {
    background-color: #9c9c9c;
  }
  
  .contact-us-textarea::-webkit-scrollbar-track {
    background-color: #f9f9fd;
  }
  
  .contact-us-input::placeholder {
    color: white;
    font-size: 14px;
  }
  
  .contact-us-input:focus {
    outline: none;
  }
  
  .contact-us-submit-btn {
    display: block;
    transition: background-color .25s ease-in-out;
    position: relative;
    width: auto;
    margin: 15px auto 5px;
    height: auto;
    padding: 10px 15px;
    font-size: 24px;
    color: white;
    background: #636363;
    border-radius: 15px;
    border: 2px solid #bb8c8c;
    cursor: pointer;
    font-family: 'Architects Daughter', cursive;
  }
  
  .contact-us-submit-btn:hover {
    background-color: #474747;
  }

  @media screen and (max-width: 1440px) {
    .contact-us-main-title {
      font-size: 28px;
    }

    .contact-us-title {
      font-size: 18px;
      margin: 12px auto;
    }

    .contact-us-input, .contact-us-input::placeholder {
      font-size: 13px;
    }

    .contact-us-input {
      margin: 18px auto 0;
    }

    .contact-us-submit-btn {
      font-size: 22px;
    }

    .contact-us-textarea {
      height: 90px;
    }
  }

  @media screen and (max-width: 1024px) {
    .contact-us-main-title {
      font-size: 24px;
    }

    .contact-us-title {
      font-size: 16px;
      margin: 10px auto;
    }

    .contact-us-input {
      margin: 20px auto 0;
      padding: 4px;
    }

    .contact-us-submit-btn {
      font-size: 20px;
    }

    .contact-us-textarea {
      height: 75px;
    }
  }

  @media screen and (max-width: 768px) {
    .contact-us-form {
      margin: 0 0 auto auto;
      width: 70%;
    }

    .contact-us-main-title {
      font-size: 20px;
    }

    .contact-us-title {
      font-size: 14px;
      margin: 8px auto;
    }

    .contact-us-input, .contact-us-input::placeholder {
      font-size: 11px;
    }

    .contact-us-input {
      margin: 16px auto 0;
      padding: 3px;
    }

    .contact-us-submit-btn {
      font-size: 16px;
      padding: 8px 12px;
      border-radius: 10px;
    }

    .contact-us-textarea {
      height: 60px;
    }
  }

  @media screen and (max-width: 425px) {
    .contact-us-main-title {
      font-size: 16px;
    }

    .contact-us-title {
      font-size: 12px;
      margin: 5px auto;
    }

    .contact-us-input, .contact-us-input::placeholder {
      font-size: 12px;
    }

    .contact-us-input {
      margin: 12px auto 0;
      padding: 2px;
    }

    .contact-us-submit-btn {
      font-size: 14px;
      padding: 6px 10px;
      border-radius: 8px;
    }

    .contact-us-textarea {
      height: 45px;
    }
  }

  @media screen and (max-width: 350px) {
    .contact-us-form {
      margin: 0 0 auto auto;
      width: 75%;
    }

    .contact-us-input, .contact-us-input::placeholder {
      font-size: 10px;
    }
  }

  @media screen and (min-width: 426px) and (max-height: 800px) {
    .contact-us-main-title {
      font-size: 20px;
    }

    .contact-us-title {
      font-size: 14px;
      margin: 8px auto;
    }

    .contact-us-input, .contact-us-input::placeholder {
      font-size: 11px;
    }

    .contact-us-input {
      margin: 16px auto 0;
      padding: 3px;
    }

    .contact-us-submit-btn {
      font-size: 16px;
      padding: 8px 12px;
      border-radius: 10px;
    }

    .contact-us-textarea {
      height: 60px;
    }
  }

  @media screen and (min-width: 426px) and (max-height: 600px) {
    .contact-us-main-title {
      font-size: 16px;
    }

    .contact-us-title {
      font-size: 12px;
      margin: 5px auto;
    }

    .contact-us-input, .contact-us-input::placeholder {
      font-size: 12px;
    }

    .contact-us-input {
      margin: 12px auto 0;
      padding: 2px;
    }

    .contact-us-submit-btn {
      font-size: 14px;
      padding: 6px 10px;
      border-radius: 8px;
    }

    .contact-us-textarea {
      height: 45px;
    }
  }

  @media screen and (min-width: 426px) and (max-height: 450px) {
    .contact-us-main-title {
      font-size: 14px;
    }

    .contact-us-title {
      font-size: 11px;
      margin: 5px auto;
    }

    .contact-us-input, .contact-us-input::placeholder {
      font-size: 11px;
    }

    .contact-us-input {
      margin: 8px auto 0;
      padding: 2px;
    }

    .contact-us-submit-btn {
      font-size: 12px;
      padding: 4px 7px;
      border-radius: 6px;
    }

    .contact-us-textarea {
      height: 30px;
    }

    .contact-us-submit-btn {
      border: 1px solid #bb8c8c;
    }
  }